.input {
    /* width: 100%;
    flex-grow: 1;
    text-align: center;
    padding: 10px; */
    width: 100%;
  max-width: 280px;
  padding: 10px 15px;
  border: none;
  outline: none;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 16px 0px 16px 0px;
  border-bottom: 3px solid rgb(190, 223, 0);
  
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  transition: 0.2s ease-out;
}
input:focus {
    background-color: rgba(255, 255, 255, 0.3);
}
input::placeholder {
    color: #fff;
}